<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Loading...</span>
        </div>
    </div>
   <DeptoBase
    :goal="molino.meta_semana"
    :progress="molino.progress"
    :name="molino.nombre"
    :depto="molino.depto"
    :days="molino.dias_laborados"
    :m3="molino.m3_persona"
    :bonus_p="molino.bono_persona"
    :bonus_tot="molino.bono_depto"
    :asis="molino.asistencia"
    :extra="molino.$_extra_m3"
    :bonus_prod="molino.bono_productividad"
    :bonus_goals="molino.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'molino',
    components: {DeptoBase},
    created() {  
        this.$store.dispatch("plants/getInfo", {
            department: 'molino',
            userCode: this.$route.params.user,
            city: 'juarez'
        });  
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        molino() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>